import Vue from "vue";
import Router from "vue-router";
import AppHeader from "./layout/AppHeader";
import AppFooter from "./layout/AppFooter";
import Index from "./views/Index.vue";
import Cart from "./views/Cart.vue";
import Checkout from "./views/Checkout.vue";
import PaymentSuccessful from "./views/PaymentSuccessful.vue";

Vue.use(Router);

export default new Router({
  mode: 'history',
  linkExactActiveClass: "active",
  routes: [{
      path: "/",
      name: "index",
      components: {
        header: AppHeader,
        default: Index,
        footer: AppFooter
      },
    },
    {
      path: "*",
      name: "index",
      components: {
        header: AppHeader,
        default: Index,
        footer: AppFooter
      },
    },
    {
      path: "/cart",
      name: "cart",
      components: {
        header: AppHeader,
        default: Cart,
        footer: AppFooter
      },
    },
    {
      path: "/checkout",
      name: "checkout",
      components: {
        header: AppHeader,
        default: Checkout,
        footer: AppFooter
      },
    },
    {
      path: "/payment-successful",
      name: "payment-successful",
      components: {
        header: AppHeader,
        default: PaymentSuccessful,
        footer: AppFooter
      },
    },
  ],
  scrollBehavior: (to) => {
    if (to.hash) {
      return {
        selector: to.hash,
      };
    } else {
      return {
        x: 0,
        y: 0,
      };
    }
  },
});