<template>
  <div>
    <div class="wrapper">
      <div class="container" v-if="products.length < 1">
        <div class="text-center my-5">
          <div class>
            <img src="img/icons/empty-cart.svg" height="150" class="img img-responsive" />
          </div>
          <p class="font-weight-bold mt-4 mb-0">Your cart is empty!</p>
          <p class="mt-1 small">Browse our categories and discover our best deals!</p>
          <router-link to="/" class="btn btn-white text-capitalize mt-4">Start Shopping</router-link>
        </div>
      </div>
      <div class="container" v-else>
        <div class>
          <div class="row">
            <div class="col my-auto">
              <p class="my-auto">
                Items in Cart -
                <span href="#" class="p-color">{{ products.length }}</span>
              </p>
            </div>
            <div class="col-auto my-auto">
              <button
                @click="emptyCart"
                id="emptyCartBtn"
                class="btn btn-white btn-sm text-capitalize"
              >
                <span class="fa fa-trash mr-2 p-color"></span> Empty Cart
              </button>
            </div>
          </div>

          <div class="mt-3 mb-4">
            <router-link to="/" class="p-color">
              <span class="fa fa-angle-left mr-2"></span> Continue Shopping
            </router-link>
          </div>
        </div>
        <div class="searchitem">
          <div class="row">
            <div class="col-md-8">
              <div class="row pr-3">
                <div
                  class="col-md-5 mb-sm-3 pr-0"
                  v-for="(product, index) in products"
                  :key="index"
                >
                  <div class="card itemcard itemselected">
                    <div class="card-body">
                      <p class="itemname mb-0">{{ product.product.product_name }}</p>
                      <p class="itemprice mb-1">
                        {{ product.product.actual_price | currency('RwF', 0) }} /
                        qty
                      </p>
                      <!-- <p>Quantity - {{product.quantity}}</p>
                      <p>Total Quantity - {{product.product.quantity}}</p>-->
                      <!-- <input
                        :value="product.quantity"
                        :max="product.product.quantity"
                        @input="quantityChange($event, product)"
                        type="number"
                        placeholder="Quantity"
                        class="form-control"
                      />-->

                      <div class="quantty mt-2 mb-3">
                        <button
                          @click="incrementQuant(index, product)"
                          class="btn cartbtn btn-custb addToCart"
                        >
                          <span class="fa fa-plus"></span>
                        </button>

                        <input
                          type="number"
                          :value="product.quantity"
                          min="1"
                          :max="product.product.quantity"
                          placeholder="Quantity"
                          class="form-control text-center font-weight-bold"
                        />

                        <button
                          @click="decrementQuant(index, product)"
                          class="btn cartbtn btn-custb addToCart"
                        >
                          <span class="fa fa-minus"></span>
                        </button>
                      </div>

                      <p
                        class="small my-2 text-danger"
                        id="90b15d3f-530e-4b00-aa96-be754504c18b"
                        hidden
                      ></p>

                      <div class="mt-2">
                        <div class="row">
                          <div class="col my-auto">
                            <span class="itemqua">
                              {{
                              calTotal(
                              product.product.actual_price,
                              product.quantity
                              ) | currency('RwF', 0)
                              }}
                            </span>
                          </div>
                          <div class="col-auto my-auto">
                            <button
                              class="btn btn-sm btn-custom removeFromCart"
                              @click="removeFromCart(product.product.id)"
                            >
                              <span class="fa fa-trash mr-2"></span>
                              Remove
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="card itemcard mb-3">
                <div class="card-body">
                  <p class="p-color">Checkout</p>
                  <div class="mt-2">
                    <p class="m-0 small">
                      TOTAL
                      <span class="f-right p-color h6">
                        {{
                        total | currency('RwF', 0)
                        }}
                      </span>
                    </p>
                  </div>
                  <hr class="m-1" />
                  <div class="mt-2">
                    <p class="m-0 small">
                      <span class>Delivery charge</span>
                      <span class="f-right p-color h6">
                        {{
                        deliveryCharge | currency('RwF', 0)
                        }}
                      </span>
                    </p>
                  </div>
                </div>
              </div>
              <button
                :disabled="products.length < 1 || cartLoading"
                @click="checkout"
                class="btn btn-block text-capitalize btn-custom"
              >
                <img v-if="cartLoading" height="18" src="img/icons/loader.svg" />
                <span v-else>
                  Proceed to Checkout
                  {{ totalWithDelivery | currency('RwF', 0) }}
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      newArray: "",
      productQuantity: [],
      statusMsg: [],
      cartLoading: false
    };
  },
  computed: {
    products() {
      return this.$store.getters.cartProducts;
    },
    deliveryCharge() {
      if (this.products.length < 1) {
        return 0;
      } else {
        return 0;
      }
    },
    total() {
      return this.$store.getters.cartTotal;
    },
    totalWithDelivery() {
      return this.total + this.deliveryCharge;
    }
  },
  mounted() {
    // console.log(this.products);
  },
  methods: {
    incrementQuant(index, product) {
      const quantity = this.products[index].quantity;
      if (quantity < product.product.quantity) {
        this.products[index].quantity++;
      }
      let increProduct = this.products[index].quantity;
      let productID = product.product.id;
    },
    decrementQuant(index, product) {
      const quantity = this.products[index].quantity;
      if (quantity > 1) {
        this.products[index].quantity--;
      }
      let increProduct = this.products[index].quantity;
      let productID = product.product.id;
    },
    checkout() {
      this.cartLoading = true;
      this.$store
        .dispatch("checkout")
        .then(resp => {
          this.cartLoading = false;
          this.$router.push({ path: "checkout" });
        })
        .catch(err => {
          this.cartLoading = false;
          if (err.data.status == 422) {
            this.statusMsg = err.data.data.checkout_errors;
          } else {
          }
          // console.log(this.statusMsg);
        });
    },
    removeFromCart(productid) {
      // console.log(productid);
      this.$store
        .dispatch("removeCartItem", productid)
        .then(resp => {})
        .catch(err => {});
    },
    emptyCart() {
      this.$store.commit("emptyCart");
    },
    calTotal(price, quantity) {
      return price * quantity;
    },
    quantityChange(event, product) {
      // console.log(event.target.value);
      // console.log(product.product.id);
      let quantity = event.target.value;
      let productID = product.product.id;
      this.$store
        .dispatch("changeQuantity", { quantity, productID })
        .then(resp => {})
        .catch(err => {});
    }
  }
};
</script>
