<template>
  <div>
    <div v-if="loading" class="my-5 text-center loadinghh">
      <img height="40" src="img/icons/loader.svg" />
      <p class="mt-2 small">Loading Products</p>
    </div>
    <div v-if="!loading" class="wrapper">
      <div v-if="noMerchant">
        <div class="container text-center">
          <img src="img/icons/not_found.svg" height="80" class="mb-3 mt-4" />
          <p>Merchant not found with provided URL!</p>
          <a href="/" class="btn btn-white text-capitalize mt-2 btn-sm">
            <span class="fa fa-home mr-2 p-color"></span>
            Visit Homepage
          </a>
        </div>
      </div>

      <div v-if="networkFail">
        <div class="container text-center">
          <img src="img/icons/emptystr.svg" height="80" class="mb-3 mt-4" />
          <p>Unable to Load Products!</p>
          <button @click="reloadPage" class="btn btn-white text-capitalize mt-2 btn-sm">Try again</button>
        </div>
      </div>
      <div v-else>
        <div v-if="noResult">
          <div class="container">
            <div class="row">
              <div class="col-md-5 mx-auto">
                <div class="text-center my-5">
                  <img src="img/icons/not_found.svg" height="150" class="mb-3 mt-4" />
                  <p>
                    Oops, it looks like we dont have a product named
                    <span
                      class="font-weight-bold"
                    >"{{ searched }}"</span>
                    in our database yet. Kindly, leave your phone number and we ll find it and call you back for delivery within 30 mins
                  </p>
                  <div class="text-center mx-auto">
                    <form @submit.prevent="submitConcierge" class="form-inline phoneForm">
                      <div class="form-group">
                        <input
                          required
                          v-model="phone"
                          type="text"
                          class="form-control phoneInput"
                          placeholder="Phone Number"
                        />
                        <button :disabled="sendingConc" class="btn text-capitalize btn-custom">
                          <img v-if="sendingConc" height="18" src="img/icons/loader.svg" />
                          <span v-else>
                            <span class="fa fa-check mr-2"></span>Submit
                          </span>
                        </button>
                      </div>
                    </form>
                  </div>
                  <button
                    @click="viewAllProduct"
                    class="btn btn-white text-capitalize mt-2 btn-sm"
                  >View all Products</button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="container">
            <div class>
              <div class="filter">
                <div v-if="vendor.available && !noMerchant" class="row mx-lg-0 my-auto">
                  <div class="col-12">
                    <p class="font-weight-bold">
                      <span>
                        Welcome to
                        <span class="text-capitalize">{{ vendor.name }}</span>'s Store
                      </span>
                    </p>
                  </div>
                </div>
                <div class="row mx-lg-0 my-auto" v-if="!noMerchant">
                  <div class="col-7">
                    <span class="my-auto pl-lg-0 text-white">Filter by Categories</span>
                  </div>
                  <div class="col-5 text-right">
                    <button
                      @click="clearFilterC"
                      v-if="clearFilterCat"
                      class="btn btn-white btn-sm text-capitalize"
                    >
                      <span class="fa fa-cut mr-2 p-color"></span> Clear Filter
                    </button>
                  </div>
                </div>
                <div id="filterCategories" class="filteritem mt-3">
                  <a
                    @click="filterProduct(category.name)"
                    :class="{ itemselected: itemselected == category.name }"
                    href="javascipt:void(0)"
                    v-for="(category, index) in categories"
                    :key="index"
                  >{{ category.name }}</a>
                </div>
                <p
                  class="small mt-3 mb-3 text-right d-lg-none"
                  v-if="!noMerchant"
                >Scroll left for more Categories</p>
                <div class="searchrel mt-3 mb-1" id="searchfield" v-if="!noResult && !noMerchant">
                  <div class>
                    <form action method="GET">
                      <div class="searchbox">
                        <form @submit.prevent="searchProduct">
                          <div class="row">
                            <div class="col-7 col-md-10 pr-0">
                              <input
                                v-model="search"
                                type="text"
                                placeholder="Search for a product"
                                class="form-control searchform"
                              />
                            </div>
                            <div class="col-5 col-md-2">
                              <button
                                type="submit"
                                class="btn btn-white btn-block text-capitalize text-black searchbtn"
                              >
                                <span class="fa fa-search mr-2"></span>
                                Search
                              </button>
                            </div>
                          </div>
                        </form>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
              <div class="my-lg-4 my-2"></div>
            </div>
            <div class="searchitem">
              <div v-if="loadingCat" class="my-5 text-center">
                <img height="40" src="img/icons/loader.svg" />
              </div>
              <div v-else>
                <div class="my-2" v-if="searchInfoo">
                  <div class="row">
                    <div class="col-6">
                      <p class>
                        Your search result -
                        <span
                          class="p-color"
                        >{{ productDyn.length }} items found</span>
                      </p>
                    </div>
                    <div class="col-6 text-right pr-3">
                      <button @click="clearSearch" class="btn btn-white btn-sm text-capitalize">
                        <span class="fa fa-cut mr-2 p-color"></span> Clear
                        Filter
                      </button>
                    </div>
                  </div>
                </div>

                <div class="row pr-lg-4 pr-3">
                  <div
                    class="col-md-3 pb-lg-3 pr-0 itemcol"
                    v-for="(product, index) in productDyn"
                    :key="index"
                  >
                    <div class="card itemcard">
                      <div class="card-body">
                        <p class="itemname mb-0">{{ product.product_name }}</p>
                        <p class="itemprice">{{ product.actual_price | currency('RwF', 0) }}</p>
                        <div class="mt-2">
                          <div class="row">
                            <div class="col">
                              <span class="itemqua">{{ product.category }}</span>
                            </div>
                            <div class="col-auto">
                              <p class="text-right m-0">
                                <span class="itemqua text-capitalize">{{ product.unit }}</span>
                              </p>
                            </div>
                          </div>

                          <div class="mt-4">
                            <div v-if="!product.addingCart" class>
                              <p class="m-0 inCart" v-if="checkInCart(product.uuid)">In Cart</p>
                              <p class="m-0 outStock" v-else-if="product.quantity < 1">Out of Stock</p>
                              <button
                                id="addtoCartbtn"
                                v-else
                                v-show="toggleInput !== product.id"
                                @click="showInput(product, index)"
                                class="btn cartbtn btn-btn-custb btn-custom addToCart btn-block"
                              >
                                <span class="fa fa-cart-plus mr-1"></span>
                                Add to Cart
                              </button>
                            </div>

                            <div v-if="toggleInput === product.id" class="inputQuantity">
                              <div class="quantty">
                                <button
                                  @click="incrementQuant(index, product)"
                                  class="btn cartbtn btn-custb addToCart minbtn"
                                >
                                  <span class="fa fa-plus"></span>
                                </button>

                                <input
                                  type="number"
                                  v-model="productQuantity[index].qty"
                                  min="1"
                                  :max="product.quantity"
                                  placeholder="Quantity"
                                  class="form-control text-center font-weight-bold"
                                />

                                <button
                                  @click="decrementQuant(index, product)"
                                  class="btn cartbtn btn-custb addToCart minbtn"
                                >
                                  <span class="fa fa-minus"></span>
                                </button>
                                <button
                                  @click="
                                    addProductCartFunc(
                                      product,
                                      productQuantity[index].qty
                                    )
                                  "
                                  class="btn cartbtn btn-custom addToCart ml-4 minbtn"
                                >
                                  <span class="fa fa-check"></span>
                                </button>
                              </div>
                              <p class="text-center mb-2 mt-3">
                                <a @click="hideInput" class="text-coll">Cancel</a>
                              </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div></div>

    <v-tour name="myTour" :steps="steps" :options="myOptions"></v-tour>
  </div>
</template>
<script>
import VueNotifications from "vue-notifications";

let qtyy = {
  qty: 1
};
export default {
  data() {
    return {
      //addingCart: false,
      productQuantity: [],
      vendor: {
        available: false,
        name: ""
      },
      noMerchant: false,
      loading: false,
      loadingCat: false,
      itemselected: "",
      sendingConc: false,
      search: "",
      networkFail: false,
      searched: "",
      sendConcierge: false,
      noResult: false,
      phone: "",
      searchInfoo: false,
      toggleInput: null,
      clearFilterCat: false,
      myOptions: {
        useKeyboardNavigation: false,
        labels: {
          buttonSkip: "Skip tour",
          buttonPrevious: "Previous",
          buttonNext: "Next",
          buttonStop: "Finish"
        }
      },
      steps: [
        {
          target: "#filterCategories", // We're using document.querySelector() under the hood
          content: `<p class="small">Scroll left for more Categories!</span>`,
          params: {
            placement: "top"
          }
        },
        {
          target: "#addtoCartbtn", // We're using document.querySelector() under the hood
          content: `<p class="small">Click to add product to Cart!</p>`,
          params: {
            placement: "top"
          }
        },
        {
          target: "#searchfield", // We're using document.querySelector() under the hood
          content: `<p class="small">Search for Products here!</p>`,
          params: {
            placement: "top"
          }
        }
      ]
    };
  },
  notifications: {
    showLoginMsg: {
      title: "",
      message: "",
      type: ""
    }
  },
  computed: {
    products() {
      return this.$store.getters.availableProducts;
    },
    cartProducts() {
      return this.$store.getters.cartProducts;
    },
    productDyn() {
      /* this.products.forEach((product, index) => {
        this.productQuantity.push(1);
      }); */
      const productdynamic = [];
      this.products.forEach(element => {
        productdynamic.push({
          ...element,
          addingCart: false,
          itemAdded: false
        });
      });
      // console.log(productdynamic);
      return productdynamic;
    },
    categories() {
      return this.$store.getters.availableCategories;
    }
  },
  mounted() {
    /* this.products.forEach((product, index) => {
      this.productQuantity.push(1);
    }); */
    // console.log(this.productDyn);
    this.$swal({
      icon: "info",
      showCancelButton: true,
      cancelButtonColor: "#000000",
      allowOutsideClick: false,
      confirmButtonText: "Yes, I agree",
      cancelButtonText: "Not of age",
      text:
        "We sell alcoholic drinks to only people of 18+ of age. Failure to show your ID upon the delivery of your order, we will cancel it",
      confirmButtonColor: "#e75563"
    }).then(result => {
      if (result.value) {
      } else {
        window.location.href = "https://google.com";
        console.log("Cancelled");
      }
    });
  },
  created() {
    this.loading = true;
    if (this.$route.params.pathMatch) {
      this.vendor.name = this.$route.params.pathMatch.substring(1);
      this.vendor.available = true;
      this.getVendorProduct(this.vendor.name);
    } else {
      this.getProductss();
    }
  },
  methods: {
    incrementQuant(index, product) {
      const quantity = this.productQuantity[index].qty;
      if (quantity < product.quantity) {
        this.productQuantity[index].qty++;
        this.productQuantity[index].qty = this.productQuantity[index].qty++;
      }

      //this.productQuantity[index] = this.productQuantity[index] + 1;
      //console.log(this.productQuantity[index]);
    },
    decrementQuant(index, product) {
      const quantity = this.productQuantity[index].qty;
      if (quantity > 1) {
        this.productQuantity[index].qty--;
      }
      //this.productQuantity[index] = this.productQuantity[index] - 1;
      //console.log(this.productQuantity[index]);
    },
    getVendorProduct(vendor) {
      this.$store
        .dispatch("fetchVendorProductProducts", vendor)
        .then(resp => {
          this.loading = false;
          let nArray = [];
          for (var i = 0; i < resp.length; i++) {
            nArray[i] = {
              qty: 1
            };
          }
          this.productQuantity = nArray;
          if (!localStorage.getItem("barttyUser")) {
            localStorage.setItem("barttyUser", true);
            this.$tours["myTour"].start();
          }
        })
        .catch(err => {
          if (err == 404) {
            console.log(
              "Merchant not found with provided URL or no products exist"
            );
            this.loading = false;
            this.noMerchant = true;
          }
          this.loading = false;
        });
    },
    getProductss() {
      this.$store
        .dispatch("fetchProducts")
        .then(resp => {
          this.loading = false;
          let nArray = [];
          for (var i = 0; i < resp.length; i++) {
            nArray[i] = {
              qty: 1
            };
          }
          this.productQuantity = nArray;
          if (!localStorage.getItem("barttyUser")) {
            localStorage.setItem("barttyUser", true);
            this.$tours["myTour"].start();
          }
        })
        .catch(err => {
          this.loading = false;
          this.networkFail = true;
        });
    },
    showNotification(title, name, MsgType) {
      this.showLoginMsg({
        title: title,
        message: name,
        type: MsgType,
        timeout: 4000
      });
    },
    submitConcierge() {
      let phone = this.phone;
      let search = this.search;

      this.sendingConc = true;
      this.$store
        .dispatch("concierge", { phone, search })
        .then(resp => {
          this.phone = "";
          this.sendingConc = false;
          this.showNotification(
            "Sent Successfully",
            resp.data.message,
            "success"
          );
        })
        .catch(err => {
          this.showNotification(
            "Sending failed!",
            "Unable to send phone number now, please try later.",
            "error"
          );
          this.sendingConc = false;
        });
    },
    showInput(product, index) {
      // this.productDyn[index];
      //product.addingCart = true;
      if (this.productDyn[index].id === product.id) {
        this.toggleInput = product.id;
      }
      this.productQuantity.push({ qty: 1 });
      //console.log((this.productDyn[index].addingCart = true));
      //console.log(product.addingCart);
      //console.log(product);
    },
    hideInput() {
      if (this.toggleInput) {
        this.toggleInput = null;
      }
    },
    checkInCart(productId) {
      const cart = this.cartProducts;
      const isAvailable = cart.find(item => item.product.uuid === productId);
      if (isAvailable) {
        // console.log("In Cart");
        return true;
      } else {
        // console.log("Not in Cart");
        return false;
      }
    },
    viewAllProduct() {
      this.search = "";
      this.noResult = false;
      this.$store
        .dispatch("searchProduct", this.search)
        .then(resp => {
          this.loading = false;
          this.noResult = false;
        })
        .catch(err => {
          if (err == 404) {
            this.searched = this.search;
            this.loading = false;
            this.noResult = true;
          }
        });
    },
    clearSearch() {
      this.searchInfoo = false;
      this.viewAllProduct();
    },
    searchProduct() {
      this.itemselected = "";
      this.clearFilterCat = false;
      this.searchInfoo = true;
      let search = this.search;
      this.loading = true;

      if (this.$route.params.pathMatch) {
        let VendorName = this.$route.params.pathMatch.substring(1);
        this.$store
          .dispatch("searchVendorProduct", { VendorName, search })
          .then(resp => {
            this.loading = false;
            this.noResult = false;
          })
          .catch(err => {
            if (err == 404) {
              this.searched = this.search;
              this.loading = false;
              this.noResult = true;
            }
          });
      } else {
        this.$store
          .dispatch("searchProduct", search)
          .then(resp => {
            this.loading = false;
            this.noResult = false;
          })
          .catch(err => {
            if (err == 404) {
              this.searched = this.search;
              this.loading = false;
              this.noResult = true;
            }
          });
      }
    },
    filterProduct(category) {
      this.itemselected = category;
      this.clearFilterCat = true;
      this.loadingCat = true;

      if (this.$route.params.pathMatch) {
        let VendorName = this.$route.params.pathMatch.substring(1);
        this.$store
          .dispatch("filterVendorProduct", { category, VendorName })
          .then(resp => {
            this.loadingCat = false;
            this.noResult = false;
          })
          .catch(err => {
            if (err == 404) {
              this.searched = this.search;
              this.loadingCat = false;
              this.noResult = true;
            }
          });
      } else {
        this.$store
          .dispatch("filterProduct", category)
          .then(resp => {
            this.loadingCat = false;
            this.noResult = false;
          })
          .catch(err => {
            if (err == 404) {
              this.searched = this.search;
              this.loadingCat = false;
              this.noResult = true;
            }
          });
      }
    },
    reloadPage() {
      location.reload();
    },
    addProductCartFunc(product, quantity) {
      let productWithQuantity = {
        product: product,
        quantity: quantity
      };
      this.$store.dispatch("addProductToCart", productWithQuantity);
      this.hideInput();
    },
    clearFilterC() {
      this.clearFilterCat = false;
      this.itemselected = "";
      this.viewAllProduct();
    }
  }
};
</script>
<style>
.phoneForm {
  margin: 0 auto;
  justify-content: center;
  margin-bottom: 20px;
}
.phoneInput {
  height: 43px;
  margin-right: 11px;
}
.v-tour__target--highlighted {
  box-shadow: 0 0 0 99999px rgba(0, 0, 0, 0.4) !important;
}
</style>
