<template>
  <div>
    <div class="wrapper">
      <div class="container">
        <div class="my-auto" v-if="processing">
          <div class="my-5 text-center">
            <img height="90" src="img/icons/loader.svg" />
            <p class="mt-3">Payment Processing. Please wait!</p>
          </div>
        </div>
        <div v-else>
          <div class>
            <router-link to="/cart" class="p-color router-link-active">
              <span class="fa fa-angle-left mr-2"></span> View Cart
            </router-link>
            <div class="filter mt-2">
              <p class="font-weight-bold">Delivery details</p>
            </div>
          </div>
          <form @submit.prevent="checkoutProduct">
            <div class="searchitem">
              <div class="row">
                <div class="col-md-8">
                  <div class="row">
                    <div class="col-md-8">
                      <div class="deliveryform">
                        <div class="row">
                          <div class="col-md-6">
                            <input
                              required
                              type="text"
                              class="form-control"
                              v-model="firstname"
                              placeholder="First name"
                            />
                          </div>
                          <div class="col-md-6">
                            <input
                              required
                              type="text"
                              class="form-control"
                              v-model="lastname"
                              placeholder="Last name"
                            />
                          </div>
                        </div>
                        <input
                          type="email"
                          class="form-control"
                          v-model="email"
                          placeholder="Email address"
                        />
                        <input
                          required
                          type="phone"
                          class="form-control"
                          v-model="phone"
                          placeholder="Phone number"
                        />

                        <textarea
                          required
                          v-model="delivery_address"
                          @change="setDeliveryAd"
                          class="form-control"
                          placeholder="Delivery Address"
                          rows="5"
                        ></textarea>

                        <select
                          required
                          @change="setDeliveryAd"
                          v-model="delivery_city"
                          class="form-control"
                        >
                          <option
                            v-for="(city, index) in cities"
                            :key="index"
                            :value="city"
                          >{{ city }}</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-md-4">
                  <div class="card itemcard mb-3">
                    <div class="card-body">
                      <p class="p-color">Checkout</p>
                      <div class="mt-2">
                        <p class="m-0 small">
                          TOTAL
                          <span class="f-right p-color h6">
                            {{
                            checkdata.sub_total | currency('RwF', 0)
                            }}
                          </span>
                        </p>
                      </div>
                      <hr class="m-1" />
                      <div class="mt-2">
                        <p class="m-0 small">
                          <span class>Delivery charge</span>
                          <span class="f-right p-color h6">
                            {{
                            checkdata.delivery | currency('RwF', 0)
                            }}
                          </span>
                        </p>
                      </div>
                      <div class="mt-4">
                        <p class="font-weight-bold m-0 small">
                          Grand Total
                          <span class="f-right p-color font-weight-bold h6">
                            {{
                            checkdata.grand_total | currency('RwF', 0)
                            }}
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                  <div class="card itemcard mb-3">
                    <div class="card-body">
                      <p class="p-color">Delivery to</p>
                      <p
                        v-if="delivery_city && delivery_address"
                        class="itemprice mb-0 small"
                        id="deliveryAddress"
                      >{{ deliveryCity }}</p>
                    </div>
                  </div>
                  <button
                    :disabled="cartLoading"
                    type="submit"
                    id="completeCheckoutBtn"
                    class="btn btn-block text-capitalize btn-success"
                  >
                    <img v-if="cartLoading" height="18" src="img/icons/loader.svg" />
                    <span v-else>
                      Pay With PayPal -
                      {{ checkdata.usd_grand_total | currency }}
                    </span>
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import VueNotifications from "vue-notifications";
export default {
  data() {
    return {
      firstname: "",
      cartLoading: false,
      lastname: "",
      email: "",
      phone: "",
      delivery_city: "",
      delivery_address: "",
      deliveryCity: "",
      payment_method: "paypal",
      processing: false
    };
  },
  notifications: {
    showLoginMsg: {
      title: "",
      message: "",
      type: ""
    }
  },
  computed: {
    cities() {
      return this.$store.getters.checkoutProduct.cities;
    },
    products() {
      return this.$store.getters.cartProducts;
    },
    cart() {
      const prepareCheckout = this.products.map(item => {
        const container = {};

        container.uuid = item.product.uuid;
        container.quantity = item.quantity;

        return container;
      });
      return prepareCheckout;
    },
    checkdata() {
      return this.$store.getters.checkoutProduct;
    },
    total() {
      return this.$store.getters.cartTotal;
    }
  },
  mounted() {
    if (this.$route.query.token) {
      this.processing = true;
      let token = this.$route.query.token;
      this.showNotification("Payment Cancelled", "Please try again!", "error");
      this.$store
        .dispatch("cancelPayment", token)
        .then(() => {
          this.processing = false;
        })
        .catch(err => {
          this.processing = false;
        });
    } else {
    }
    // this.processing = true;

    // console.log(this.cart);
  },
  methods: {
    showNotification(title, name, MsgType) {
      this.showLoginMsg({
        title: title,
        message: name,
        type: MsgType,
        timeout: 4000
      });
    },
    setDeliveryAd() {
      this.deliveryCity = this.delivery_address + ", " + this.delivery_city;
    },
    checkoutProduct() {
      this.cartLoading = true;
      let completeCheck = {
        firstname: this.firstname,
        lastname: this.lastname,
        email: this.email,
        phone: this.phone,
        delivery_address: this.delivery_address,
        delivery_city: this.delivery_city,
        payment_method: this.payment_method,
        cart: this.cart
      };

      this.$store
        .dispatch("completeCheckout", completeCheck)
        .then(resp => {
          console.log(resp.data.data);
          this.showNotification(
            "Order Created",
            "Redirecting to Payment Gateway!",
            "success"
          );
          setTimeout(() => {
            this.cartLoading = false;
            window.location = resp.data.data.payment_link;
          }, 2000);
          // this.$router.push({ path: "checkout" });
        })
        .catch(err => {
          if (err) {
            console.log(err.status);
            if (err.status == 422) {
              this.showNotification(
                "Validation Error",
                err.data.errors.phone[0],
                "info"
              );
            } else {
              this.showNotification(
                "Checkout Failed",
                "Error while checking out, please try again",
                "info"
              );
            }
          } else {
            this.showNotification(
              "Checkout Failed",
              "Error while checking out, please try again",
              "info"
            );
          }
          this.cartLoading = false;
        });
    }
  }
};
</script>
