<template>
  <div>
    <div class="wrapper">
      <div class="container">
        <div class="my-auto" v-if="processing">
          <div class="my-5 text-center">
            <img height="90" src="img/icons/loader.svg" />
            <p class="mt-3">Payment Confirmation in process. Please wait!</p>
          </div>
        </div>
        <div v-else class="text-center my-auto">
          <img
            src="img/icons/payment-success.svg"
            height="200"
            class="img img-responsive"
          />
          <p class="mt-4">Payment Successful and Order Updated</p>
          <router-link to="/" class="btn btn-white text-capitalize mt-2 btn-sm">
            <span class="fa fa-home mr-2 p-color"></span> Go Home
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      processing: true,
    };
  },
  computed: {
    cart() {
      const prepareCheckout = this.$store.getters.cartProducts.map((item) => {
        const container = {};

        container.uuid = item.product.uuid;
        container.quantity = item.quantity;

        return container;
      });
      return prepareCheckout;
    },
  },
  created() {
    // console.log(this.$route.query.token);
    // console.log(this.cart);
    this.processing = true;
    let token = this.$route.query.token;
    let confirmaPayCart = {
      token: token,
      cart: this.cart,
    };
    this.$store
      .dispatch("confirmPayment", confirmaPayCart)
      .then(() => {
        this.processing = false;
        this.$store.commit("emptyCart");
      })
      .catch((err) => {
        this.processing = false;
      });
  },
};
</script>
